import React from "react";
import "./detailedPane.scss";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faWindowClose,
} from "@fortawesome/free-solid-svg-icons";

export default function DP(props){
    return (
    <div className="detailedPane">{props.content}
    <FontAwesomeIcon icon={faWindowClose} size="4x" className="closeIcon" onClick={props.detailedPaneClose}/>
    <img style={{objectFit: "cover"}} src={props.src} alt="Netflix Origin"></img>
    </div>
    )
}