import React, { useState, useEffect } from "react";
import "./SliderItems.scss";

export default function ImgItems(props) {
  const [isActive, setIsActive] = useState(false);
  const handleClick = () => {
    props.closeDP();
    setTimeout(() => {
      setIsActive(true);
      props.openDP(props.id, props.src);
    }, 100);
  };
  useEffect(
    function () {
      if (!props.isLocalDPOpened) setIsActive(false);
    },
    [props.isLocalDPOpened]
  );
  return (
    <>
      <div
        className={"tile" + (isActive ? " tile--active" : "")}
        onClick={handleClick}
        style={{ pointerEvents: props.pointerEvent }}
      >
        <div className="tile__media">
          <img className="tile__img" src={props.src} alt="" />
        </div>
        <div className="tile__details">
          <div className="tile__title">Top Gear</div>
        </div>
      </div>
    </>
    // }
    //     </>
  );
}
